import ApiService from "@/services/api.service";
import JwtService from "@/services/jwt.service";
import router from "@/router";

const state = {
  jwt: { access: null, refresh: null },
  isAuthenticated: null,
  currentRole: null,
};

const getters = {
  currentUser(state) {
    return state.user;
  },
  isAuthenticated(state) {
    return state.isAuthenticated;
  },
  getCurrentRole(state) {
    return state.currentRole;
  },
};

const actions = {
  login(context, credentials) {
    return new Promise((resolve, reject) => {
      ApiService.post("unsecured/doLogin", credentials)
        .then(({ data }) => {
          console.log(data);
          context.commit("SET_TOKEN", data);
          resolve(data);
        })
        .catch(({ response }) => {
          console.log(response.data);
          reject(response);
        });
    });
  },
  logOut(context) {
    ApiService.post("logout/", {
      refresh: JwtService.getRefresh(),
    })
      .then(() => {
        context.commit("PURGE_AUTH");
        router.push("/login").then(() => {
          context.commit(
            "SET_MESSAGE",
            { content: "Logged Out", type: 2 },
            {
              root: true,
            }
          );
        });
      })
      .catch(() => {});
  },
  // register(context, credentials) {
  //   return new Promise(resolve => {
  //     ApiService.post("login", credentials)
  //       .then(({ data }) => {
  //         context.commit("SET_AUTH", data);
  //
  //         resolve(data);
  //       })
  //       .catch(({ response }) => {
  //         context.commit("SET_ERROR", response.data.errors, { root: true });
  //       });
  //   });
  // },
  verifyAuth({ commit }) {
    return new Promise((resolve) => {
      ApiService.post("unsecured/validateToken", JwtService.getToken())
        .then(({ data }) => {
          resolve(data);
        })
        .catch(({ response }) => {
          commit(
            "SET_MESSAGE",
            { content: response.data, type: 1 },
            {
              root: true,
            }
          );
        });
    });
  },
  // updatePassword(context, payload) {
  //   return ApiService.put("password", payload).then(({ data }) => {
  //     context.commit("SET_PASSWORD", data);
  //     return data;
  //   });
  // }
};

const mutations = {
  SET_TOKEN(state, payload) {
    state.jwt = payload;
    JwtService.saveToken(state.jwt);
  },
  SET_AUTH(state, payload) {
    state.isAuthenticated = true;
    state.user = payload;
    state.currentRole = payload.usertype;
  },
  PURGE_AUTH(state) {
    router.push("/login").then(() => {
      state.isAuthenticated = false;
      state.user = {};
      JwtService.destroyToken();
      state.errors = {};
    });
  },
};

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters,
};
